import { useState } from "react";
import "./App.css";
import NumBtn from "./NumBtn";


const numArr = [
  { char: "C", cls: "A12" },
  { char: "/", cls: "A3" },
  { char: "*", cls: "A4" },
  { char: "7", cls: "B1" },
  { char: "8", cls: "B2" },
  { char: "9", cls: "B3" },
  { char: "-", cls: "B4" },
  { char: "4", cls: "C1" },
  { char: "5", cls: "C2" },
  { char: "6", cls: "C3" },
  { char: "+", cls: "C4D4" },
  { char: "1", cls: "D1" },
  { char: "2", cls: "D2" },
  { char: "3", cls: "D3" },
  { char: "0", cls: "E1" },
  { char: "00", cls: "E2" },
  { char: ".", cls: "E3" },
  { char: "=", cls: "E4" },
];

function App() {
  const [numString, setNumString] = useState("");
  return (
    <div className="App">
      <div>
        <input
          className="input-text"
          type="text"
          name="input"
          value={numString}
          readOnly
        />
      </div>
      <div className="container">
        {numArr.map((curElem,index) => {
          return (
            <div className={curElem.cls} k={index}>
              <NumBtn
                char={curElem.char}
                numString={numString}
                setNumString={setNumString}
                
              />
            </div>
          );
        })}
      </div>
    </div>

   
  );
}

export default App;
