import React from "react";
import "./NumBtn.css";

const NumBtn = ({ char, numString, setNumString ,k}) => {
  const handleClick = (c) => {
    try {
      // document.getElementById(char).style.transform = 'scale(0.98)'
      // let bool=numString.includes('//'||'++'||'--'||'**') && alert('please enter correct format')

      if (c === "=") {
        let result = eval(numString);

        if (result === Infinity || isNaN(result)) {
          console.log("hello", typeof result);
          alert(`${result} Please enter valid input`);
          setNumString("");
        } else {
          setNumString(result);
        }
      } else if (c === "C") {
        setNumString("");
      } else {
        setNumString((prev) => prev + c);
      }
    } catch (e) {
      alert("please enter valid input values");
      console.log(e);
      setNumString("");
    }
  };

  let outer = "";
  let inner = "";
  switch (char) {
    case "+":
      outer = `outer-plus`;
      inner = `inner-plus`;
      break;
    case "C":
      outer = `outer-clear`;
      inner = `btn-inner inner-clear`;
      break;
    case "=":
      outer = `outer-equal`;
      inner = `inner-equal`;
      break;
    default:
      outer = ``;
      inner = ``;
  }

  return (
    <div className={`btn-outer ${outer}`} >
      <div className={`btn-inner ${inner}`} onClick={() => handleClick(char)} >
        {char}
      </div>
    </div>
  );
};

export default NumBtn;
